import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import 'rxjs/operator/map';

import { Application } from '../../models'
import { environment } from '../../../environments/environment';

@Injectable()
export class AppService {
    
    constructor(private http: HttpClient) { }

    getApplication(userId){
        return this.http.get(`${environment.apiUrl}/app/${userId}`)
    }

    createApp(appData: Application){
        return this.http.post(`${environment.apiUrl}/app/new`,appData)
    }

    delete(appId){
        return this.http.delete(`${environment.apiUrl}/app/${appId}`)
    }
}


