import { Component, OnInit, ViewChild } from '@angular/core';
import { MdDialog } from '@angular/material'

import { AppService } from './app.service';
import { UserService } from '../../shared';
import { Application } from '../../models'

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  @ViewChild("newAppDialog") newAppDialog

  applications:[Application] = [
    {
      _id:1,
      name:"Sample App"
    },
    {
      _id:1,
      name:"Sample App"
    }
  ];
  newAppName:string="";
  constructor(
    private appService: AppService, 
    public userService: UserService,
  ) {}

  ngOnInit() {
    this.appService.getApplication(this.userService.currentUser._id)
      .subscribe((apps:[Application]) => this.applications = apps)
  }

  createNew(appName:string){
    this.appService.createApp({name: appName, user: this.userService.currentUser._id})
      .subscribe((data:Application) => {
        this.applications.push(data);
      },
      (err) => {
        console.error('Failed to create application ',err);
      });
  }

  deleteApp(appId:string){
    this.appService.delete(appId)
      .subscribe((data:string) => {
        this.applications = <[Application]>this.applications.filter((app) => {
          return app._id != appId;
        })
      },(err) => {

      })
  }

}



